import React from 'react';
import { Activity } from '../query';
import * as styles from './styles.module.scss';
import dayjs from 'dayjs';
import { Truncate } from '../../text';
import ScrollAnimation from 'react-animate-on-scroll';

type props = {
    activity: Activity
    idx: React.Key
}

export default ({ activity, idx }: props) => {
    return (
        <li key={idx}>
            <ScrollAnimation
                animateIn="fadeIn"
            >
                <div className={styles.itemHeader}>
                    <div className={styles.date}>
                        <h4 className="mb-1">{dayjs(activity.date).format("MMM Do")}</h4>
                        <div>
                            <span className="badge badge-dark">
                                {activity.activity_types[0]?.name}
                            </span>
                        </div>
                    </div>
                    <h3 className={`h4 ${styles.title}`}>{activity.title}</h3>
                </div>
                <div className={styles.divider}></div>
                <div className={styles.content}>

                    <img
                        src={activity.Cover.file.publicURL}
                        alt={activity.Cover.captions}
                        className='float-left img-fluid'
                        style={{ maxWidth: '120px', marginRight: "0.5rem" }}
                    />
                    <Truncate
                        className="text-justify"
                        text={activity.description}
                        max={500}
                        expandable={true} />
                </div>
            </ScrollAnimation>
        </li>
    );
}