import React from 'react';
import * as styles from './styles.module.scss';
import { Section, SectionType } from '../../layout';
import { Container, Row, Col } from 'reactstrap';
import { useStaticQuery, graphql } from 'gatsby';
import { QueryResult, Activity } from '../query';
import Item from './item';
import dayjs from 'dayjs';
import ScrollAnimation from 'react-animate-on-scroll';

export default () => {
    const activities = useStaticQuery(graphql`
        query activityListing {
            allStrapiActivity(sort: {fields: date, order: DESC}) {
                edges {
                node {
                    date
                    Cover {
                    caption
                    file {
                        publicURL
                    }
                    }
                    title
                    venue
                    description
                    activity_types {
                        name
                    }
                }
                }
            }
        }
    `) as QueryResult['data'];

    return (
        <Section type={SectionType.darkOverWhite}>
            <Container>
                <h3 className="text-center mb-5 decorated"><span>Activities Timeline</span></h3>
                <Row className="justify-content-center">
                    <Col md="10">
                        {/* <h2 className={styles.timelineHeading}>2020</h2>
                        <ul className={styles.timeline}>
                            {activities.allStrapiActivity.edges.map(({ node }, idx) => {
                                return <Item key={idx} activity={node} idx={idx} />
                            })}
                        </ul> */}

                        {groupByYear(activities).map((grp, idx) => {
                            return (
                                <div key={idx}>
                                    <h2 className={styles.timelineHeading}>{grp.year}</h2>
                                    <ul className={styles.timeline}>
                                        {grp.activities.map((node, idx) => {
                                            return (
                                                <Item key={idx} activity={node} idx={idx} />
                                            )
                                        })}
                                    </ul>
                                </div>
                            );
                        })}
                    </Col>
                </Row>
            </Container>
        </Section>
    );

}

type yearObj = {
    year: string,
    activities: Activity[],
}

function groupByYear(activities: QueryResult['data']) {
    let yearly: yearObj[] = [];

    const getYearObj = (year: string) => {
        let obj = yearly.reduce<yearObj | null>((old, curr) => {
            if (curr.year === year) {
                return curr;
            }

            return old;
        }, null);

        if (obj == null) {
            obj = { year: year, activities: [] }
            yearly.push(obj);
        }

        return obj;
    }

    activities.allStrapiActivity.edges.forEach(({ node: a }) => {
        getYearObj(dayjs(a.date).format("YYYY"))
            .activities
            .push(a);
    });

    return yearly;
}